export default {
	data() {
		return {
			patient_id: '',
			patient_info: {},
		}
	},
	onLoad(options) {
		this.patient_id = options.id
		this.fetchPatientInfo(this.patient_id)
	},
	methods: {
		// 获取患者信息
		fetchPatientInfo(id) {
			uni.request({
				url: `https://ciai.le-cx.com/index.php/api/patient/info?id=${this.patient_id}`,
				success: res => {
					this.patient_info = res.data.data
				}
			})
		}
	}
}
